<script>
  import { onMount } from 'svelte';
  import { link } from "svelte-routing"; 
  import { Autoplay, Pagination, EffectFade } from 'swiper';
  import { Swiper, SwiperSlide } from 'swiper/svelte';

  import 'swiper/css';
  import 'swiper/css/effect-fade';
  import 'swiper/css/pagination';

  export let datas;

  let mains = [];
  const random = Math.floor(Math.random() * 15);
  datas.then((value) => {
      mains = value.slice(random,random + 5);
  });

  /// 
  let swiper;
  let btnPause;
  let btnPlay;
  let cnt = false;

  //     
  onMount(() => {
      //bind      =>     
      const swiperinstance = document.querySelector('.mainSwiper').swiper;
      swiper = swiperinstance;
  });

  ///  
  const onHandleClick = () => {
      if(cnt){ //  =>   
          btnPause.classList.remove('active');
          btnPlay.classList.add('active');
          swiper.autoplay.start(); //
      }
      else{ // 
          btnPause.classList.add('active');
          btnPlay.classList.remove('active');
          swiper.autoplay.stop(); //
      }
      cnt = !cnt;
  }
</script>
<main>
  <Swiper
      modules={[Autoplay, Pagination, EffectFade]}
      effect={'fade'}
      autoplay={{
          delay: 3000,
          ableOnInteraction: false,
      }}
      pagination={{
          clickable: true,
      }}
      bind:this={swiper}
      class="mainSwiper"
  >
      {#each mains as main, index(main) }
          <SwiperSlide>
              <img src={`https://image.tmdb.org/t/p/original/${main.backdrop_path}`} alt={main.title} /><br />
              <h3>{main.title}</h3>
              <p>
                  {#if main.overview === ''}
                        [{main.title}] ?
                  {:else}
                      {main.overview}
                  {/if}
              </p>
              <a href={`/now/${main.id}`} use:link></a>
          </SwiperSlide>
      {/each}
  </Swiper>
  <!-- play/pause -->
  <div class="swiper-playpause">
      <button 
          class="btn_pause" 
          bind:this={btnPause}
          on:click={onHandleClick}
      ></button>
      <button
          class="btn_play active"
          bind:this={btnPlay}
          on:click={onHandleClick}
      ></button>
  </div>
</main>
